.maxPlayer-fullScreen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 200;
}
.maxPlayer-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: transparent;
  color: #edefef;
}
.maxPlayer-header.vux-header .vux-header-left .left-arrow:before {
  border-color: #fff;
}
.maxPlayer-header .vux-header-title {
  margin: 0 40px;
  font-size: 16px;
}
.maxPlayer-bg {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: top center;
}
.maxPlayer-box {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
}
.maxPlayer-box .van-nav-bar {
  background: transparent;
}
.maxPlayer-box .van-nav-bar .van-icon-arrow-left,
.maxPlayer-box .van-nav-bar .van-nav-bar__text {
  color: #23e379;
}
.maxPlayer-box .van-nav-bar .van-nav-bar__title {
  color: #fff;
}
.maxPlayer-box .van-nav-bar .van-nav-bar__text:active {
  background-color: transparent;
}
.maxPlayer-box .van-hairline--bottom::after {
  border-bottom-width: 0;
}
.maxPlayer-box .maxPlayer-top {
  text-align: center;
}
.maxPlayer-box .maxPlayer-top h2 {
  color: #edefef;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
}
.maxPlayer-box .maxPlayer-top h2:before,
.maxPlayer-box .maxPlayer-top h2:after {
  content: '';
  margin: 0 5px;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  border-bottom: #edefef 1px solid;
}
.maxPlayer-bottom {
  position: fixed;
  bottom: 20px;
  left: 0;
  right: 0;
  padding: 0 15px;
  text-align: center;
  z-index: 200;
}
.maxPlayer-bottom .maxPlayer-btn {
  height: 68px;
  left: 68px;
}
.maxPlayer-bottom .maxPlayer-btn span {
  display: inline-block;
  vertical-align: middle;
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-mode {
  float: left;
  width: 38px;
  height: 38px;
  line-height: 38px;
  margin-top: 15px;
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-mode i {
  font-size: 22px;
  color: #edefef;
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-prev {
  width: 42px;
  height: 42px;
  line-height: 38px;
  text-align: center;
  border-radius: 50%;
  margin-right: 10px;
  background: rgba(35, 227, 121, 0.8);
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-prev i {
  font-size: 20px;
  color: #ffffff;
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-play {
  width: 68px;
  height: 68px;
  line-height: 80px;
  text-align: center;
  border-radius: 50%;
  background: rgba(35, 227, 121, 0.8);
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-play i {
  font-size: 40px;
  color: #ffffff;
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-play i.icon-bofangsanjiaoxing {
  margin-left: 10px;
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-next {
  width: 42px;
  height: 42px;
  line-height: 38px;
  text-align: center;
  border-radius: 50%;
  margin-left: 10px;
  background: rgba(35, 227, 121, 0.8);
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-next i {
  font-size: 20px;
  color: #ffffff;
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-listShow {
  float: right;
  width: 38px;
  height: 38px;
  line-height: 38px;
  margin-top: 15px;
}
.maxPlayer-bottom .maxPlayer-btn .maxPlayer-btn-listShow i {
  font-size: 24px;
  color: #edefef;
}
.maxPlayer-rangeBox {
  padding: 5px 0;
  margin: 0 30px 14px;
  position: relative;
}
.maxPlayer-rangeBox .maxPlayer-rangeBox-currentTime {
  position: absolute;
  top: -4px;
  left: -40px;
  color: #ffffff;
  width: 40px;
  text-align: center;
  font-size: 12px;
}
.maxPlayer-rangeBox .maxPlayer-rangeBox-timelength {
  position: absolute;
  top: -4px;
  right: -40px;
  color: #ffffff;
  width: 40px;
  text-align: center;
  font-size: 12px;
  line-height: 20px;
}
.maxPlayer-rangeBox .maxPlayer-range {
  margin: 0 !important;
}
.maxPlayer-rangeBox .maxPlayer-range .range-bar-disabled {
  opacity: 1;
}
.maxPlayer-rangeBox .maxPlayer-range .range-min,
.maxPlayer-rangeBox .maxPlayer-range .range-max {
  display: none;
  color: #ffffff;
}
.maxPlayer-rangeBox .maxPlayer-range .range-handle {
  background-color: #fff;
  width: 15px;
  height: 15px;
  top: -6px !important;
}
.maxPlayer-rangeBox .maxPlayer-range .range-quantity {
  background-color: #5dafac;
}
.maxPlayer-lrc {
  margin: 20px 0 30px;
  overflow: hidden;
}
.maxPlayer-lrc .xs-container {
  transition: all 0.5s linear;
}
.maxPlayer-lrc .song-list-scroll {
  height: 60vh;
  overflow: hidden;
}
.maxPlayer-lrc .maxPlayer-lrc-list {
  text-align: center;
  color: #ffffff;
  font-size: 14px;
  line-height: 24px;
}
.maxPlayer-lrc .maxPlayer-lrc-list li.act {
  color: #23e379;
}
.slide-bottom-leave-active,
.slide-bottom-enter-active {
  transition: all 0.5s ease;
}
.slide-bottom-enter,
.slide-bottom-leave-to {
  transform: translateX(100%);
}
